body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #58678C;
}

.color-grid {
  display: grid;
  margin: 20px;
  grid-template-columns: repeat(6, minmax(0, 1fr));
  grid-auto-rows: 200px;
  grid-gap: 20px;
}

.color-track {
  -webkit-box-shadow: 0 3px 10px #000;
          box-shadow: 0 3px 10px #000;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.color-track:hover {
  -webkit-transform: translateY(-10px);
      -ms-transform: translateY(-10px);
          transform: translateY(-10px);
  -webkit-box-shadow: 0 8px 20px #000;
          box-shadow: 0 8px 20px #000;
}

.color-track-red {
  background: #EE4D4D;
}
.color-track-red :hover {
  background: #EE4D4D;
}

.color-track-pink {
  background: #FF4794;
}

.color-track-orange {
  background: #EE904D;
}

.color-track-yellow {
  background: #EBEE4D;
}

.color-track-green {
  background: #92F76F;
}

.color-track-blue {
  background: #5CA0F0;
}


